import 'babel-polyfill'
import Vue from 'vue'
import FastClick from 'fastclick'
import router from './router'
import App from './App'
import infiniteScroll from 'vue-infinite-scroll'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import dateFormat from 'vux/src/tools/date/format.js'

import { guid } from './storage'

FastClick.attach(document.body)
Vue.config.productionTip = false
Vue.use(infiniteScroll)
Vue.use(AjaxPlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)

Vue.prototype.$baseUrl = 'https://n5.m.cc/'


Vue.prototype.$imgSrc = (src) => {
if (src) {
src = src.includes('http') ? src : Vue.prototype.$baseUrl + src
} else {
src = require('images/not_found_small.png')
}
return src
}


Vue.prototype.$setErrorImg = (e) => {
e.target.src = require('images/not_found_small.png')
}

Vue.prototype.$dateFormat = (time, type = 'YYYY-MM-DD HH:mm:ss') => {
let date = time ? new Date(time * 1000) : new Date()
return dateFormat(date, type)
}

let queryParse = (str) => {
if (!str || str === '0') {
return {}
}
let dataArr = str.split('&')
let params = {}
dataArr.forEach(query => {
let queryItem = query.split('=')
if (queryItem.length === 1) {
params.id = queryItem[0]
} else {
params[queryItem[0]] = queryItem[1]
}
})
return params
}

let href = window.location.href.replace(/#/g, '')
if (href.includes('?')) {
let search = href.slice(href.indexOf('?') + 1, href.length)
let params = queryParse(search)
if (params.device) {

guid.set(params.device)
}
}
/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



