import Vue from 'vue'
import Router from 'vue-router'
// import Index from '@/components/index'
const Index = resolve => require(['@/components/index'], resolve)
const BlockDetail = resolve => require(['@/components/block/detail'], resolve)
const BlockTrade = resolve => require(['@/components/block/trade/index'], resolve)
const BlockRecord = resolve => require(['@/components/block/trade/record'], resolve)
const TokenDetail = resolve => require(['@/components/token'], resolve)
const TokenRecord = resolve => require(['@/components/token/record'], resolve)
const VoteDetail = resolve => require(['@/components/vote/detail'], resolve)
const MortgageDetail = resolve => require(['@/components/mortgage/detail'], resolve)
const ExchangeDetail = resolve => require(['@/components/exchange/detail'], resolve)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    { // 区块详情
      path: '/block/detail/:cmd/:args',
      name: 'BlockDetail',
      component: BlockDetail
    },
    { // 交易详情
      path: '/block/trade/:args',
      name: 'BlockTrade',
      component: BlockTrade
    },
    { // 交易记录
      path: '/block/record',
      name: 'BlockRecord',
      component: BlockRecord
    },
    { // Token详情
      path: '/token/detail/:cc',
      name: 'TokenDetail',
      component: TokenDetail
    },
    { // Token交易记录
      path: '/token/record',
      name: 'TokenRecord',
      component: TokenRecord
    },
    { // 投票合约详情
      path: '/vote/detail/:id',
      name: 'VoteDetail',
      component: VoteDetail
    },
    { // 抵押合约详情
      path: '/mortgage/detail/:id',
      name: 'MortgageDetail',
      component: MortgageDetail
    },
    { // 兑换合约详情
      path: '/exchange/detail',
      name: 'ExchangeDetail',
      component: ExchangeDetail
    }
  ]
})



